<template>
  <multiselect
    v-model="selectedHero"
    selectLabel=""
    deselectLabel=""
    :placeholder="$t('selectHero.Select hero')"
    :value="selectedHero"
    :options="heroes"
    :searchable="true"
    :hideSelected="true"
    :allow-empty="false"
    :clear-on-select="false"
    :maxHeight="480"
    @select="onSelect"
  >
    <template slot="singleLabel" slot-scope="{ option }">
      <div class="placeholder" v-if="Object.entries(selectedHero).length === 0">
        {{ $t("selectHero.Select hero") }}
      </div>

      <div class="selected" v-else>
        <Portrait
          folder="/images/heroes_portraits/small"
          :name="selectedHero.name.en"
          :width="46"
          :height="30"
        />
        <span>{{ option.name[$i18n.locale] }}</span>
      </div>
    </template>

    <template slot="option" slot-scope="{ option }">
      <div class="option">
        <Portrait
          folder="/images/heroes_portraits/small"
          :name="option.name.en"
          :width="46"
          :height="30"
        />
        <span>{{ option.name[$i18n.locale] }}</span>
      </div>
    </template>
  </multiselect>
</template>

<script>
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.min.css";

export default {
  name: "SelectHero",
  components: {
    Multiselect,
    Portrait: () => import("@/components/damageCalculator/Portrait.vue")
  },
  props: {
    heroes: {
      value: Array,
      default: [],
      required: false
    }
  },
  data() {
    return {
      selectedHero: {}
    };
  },
  methods: {
    onSelect(value) {
      this.$emit("select", value);
    }
  }
};
</script>

<style lang="scss">
.multiselect {
  min-height: 48px;

  .selected,
  .option {
    display: flex;
    align-items: center;
    font-size: 1.125rem;

    img {
      margin-right: 10px;
    }
  }
}
</style>
